@import url(https://fonts.googleapis.com/css?family=Alfa+Slab+One);
html, body, #root {
  width: 100vw;
  height: auto;
  overflow-x: hidden;
  scroll-behavior: smooth;
  font-family: 'Alfa Slab One', cursive !important;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: rgba(211, 184, 141, 1);
}

.header {
    width: 100%;
    height: 90px;
    padding: 0;
    margin: 0;
    position: relative;
    z-index: 3;
}

.header a {
    -webkit-text-emphasis: none;
            text-emphasis: none;
    text-decoration: none;
}

.header .menu-container {
    width: 100%;
    height: inherit;
    padding: 0;
    margin: 0;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: rgba(203, 58, 54, 1);
    box-shadow: 1px 2px 12px 3px rgba(54, 58, 54, .6);
}

/* .header .menu-container .menu-icon, */
.header .menu-container .menu-icon {
    display: none;
    padding: 20px;
    margin: 0;
    font-size: 200%;
}

.header .menu-container .logo {
    padding: 0;
    width: 400px;
    margin: 0;
}

.header .menu-container .logo h1 {
    padding: 0 0 0 30px;
    margin: 0;
    color: rgba(211, 184, 141, 1);
    cursor: pointer;
}

.header .menu-container .menu,
.header .menu-container .menu .menu-items {
    display: flex;
    /* flex-grow: 2; */
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
}

.header .menu-container .menu {
    padding-right: 10px;
}

.header .menu-container .menu .menu-items {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    flex-wrap: wrap;
}

.header .menu-container .menu .menu-items .menu-single-item {
    list-style: none;
    padding: 0;
    margin: 0;
}

.header .menu-container .menu .menu-items .menu-single-item a {
    text-decoration: none;
    color: rgba(211, 184, 141, 1);
    font-weight: bold;
    font-size: 120%;
    padding: 20px 30px;
    margin: 0;
    -webkit-transition-property: background-color color;
    transition-property: background-color color;
    -webkit-transition-duration: 0.8s;
            transition-duration: 0.8s;
}

.header .menu-container .menu .menu-items .menu-single-item a:hover {
    background-color: rgba(211, 184, 141, 1);
    color: rgba(203, 58, 54, 1);
}
@media screen and (max-width: 1024px) {
    .header .menu-container .menu .menu-items .menu-single-item a {
        font-size: 180%;
    }
}

@media screen and (max-width: 770px) {
    .header .menu-container .menu .menu-items .menu-single-item a {
        padding: 10px 20px;
    }
}
@media screen and (max-width: 700px) {
    /* .header .menu-container .menu-icon {
        display: inline;
        color: rgba(211, 184, 141, 1);
        cursor: pointer;
    } */
    .header .menu-container .menu-icon {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        align-content: center;
        cursor: pointer;
        z-index: 3;
        height: 25px;
        width: 36px;
        margin: 0;
        padding: 10px;
        -webkit-transition-property: justify-content;
        transition-property: justify-content;
        -webkit-transition-duration: .6s;
                transition-duration: .6s;
    }

    .header .menu-container .menu-icon:hover {
        justify-content: center;
    }

    .header .menu-container .menu-icon hr {
        width: 98%;
        height: 3px;
        margin: 0;
        padding: 0;
        background-color: rgba(211, 184, 141, 1);
        border: none;
        -webkit-transition-property: display, -webkit-transform;
        transition-property: display, -webkit-transform;
        transition-property: display, transform;
        transition-property: display, transform, -webkit-transform;
        -webkit-transition-duration: .6s;
                transition-duration: .6s;
    }

    .header .menu-container .logo {
        width: 400px;
    }

    .header .menu-container .menu-icon:hover .menu-icon-bar1 {
        position: relative;
        /* top: 20px; */
        -webkit-transform: rotate(50deg);
                transform: rotate(50deg);
    }
    .header .menu-container .menu-icon:hover .menu-icon-bar2 {
        position: relative;
        /* bottom: -35px; */
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
    }
    .header .menu-container .menu-icon:hover .menu-icon-bar3 {
        display: none;
    }
    
    .header .menu-container .menu {
        position: absolute;
        display: none;
        justify-content: flex-end;
        align-items: center;
        -webkit-transition-property: display;
        transition-property: display;
        -webkit-transition-duration: 2s;
                transition-duration: 2s;
        padding: 0;
        /* z-index: 2; */
    }

    .header .menu-container .menu .menu-items {
        position: relative;
        z-index: 2;
        justify-content: space-between;
        align-items: center;
        flex-direction: column;
        flex-wrap: unset;
        width: 100vw;
        height: 40vh;
        top: 25vh;
        background-color: rgba(211, 184, 141, 1);
        padding: 5px;
        padding: 10px 0;
        margin: 0;
        -webkit-transition-property: height;
        transition-property: height;
        -webkit-transition-duration: 1.5s;
                transition-duration: 1.5s; 
    }

    .header .menu-container .menu .menu-items .menu-single-item {
        padding: 0;
        width: 80%;
        text-align: center;
    }

    .header .menu-container .menu .menu-items .menu-single-item a {
        color: rgba(203, 58, 54, 1);
        padding: 0;
        display: block;
        width: 100%;
        height: 70px;
        /* font-size: 120%; */
        display: flex;
        justify-content: center;
        align-items: center;
    }
    /* .header .menu-container .menu .menu-items .menu-single-item:hover {
        background-color: rgba(203, 58, 54, 1);
    } */
    .header .menu-container .menu .menu-items .menu-single-item a:hover {
        background-color: rgba(203, 58, 54, 1);
        color: rgba(211, 184, 141, 1);
    }
}

@media screen and (max-width: 370px) {
    .header .menu-container .menu .menu-items .menu-single-item a {
        font-size: 150%;
    }
}
.feature-container{
    width: 100%;
    height: 88%;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    position: relative;
    z-index: 1;
    padding: 10px 0;
}

.feature-container .feature-ironman {
    /* flex-grow: 1; */
    position: absolute;
    width: 400px;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    /* align-self: flex-start; */
    padding: 10px;
    left: 15px;
}

.feature-container .feature-ironman h1{
    margin: 0;
    padding: 0;
    color: rgba(203, 58, 54, 1);
    font-weight: bolder;
    font-size: 350%;
    background-color: rgba(211, 184, 141, 1);
    padding: 5px;
}

.feature-container .feature-photo {
    width: 98%;
    height: 100%;
}

.feature-container .feature-photo img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

@media screen and (max-width: 920px) {
    .feature-container {
        flex-direction: column;
    }
    .feature-container .feature-ironman {
        position: static;
    }
}

@media screen and (max-width: 559px) {
    .feature-container {
        padding: 0;
    }

    .feature-container .feature-ironman {
        width: 100% !important;
        padding: 10px 0;
    }
    .feature-container .feature-ironman h1 {
        padding-left: 0;
        padding-right: 0;
        font-size: 180%;
    }

    .feature-container .feature-photo {
        height: auto;
        width: 100%;
    }
}
.about {
    width: 100%;
    height: auto;
    padding: 0;
    margin: 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
}

.about .about-item {
    width: 95%;
    height: auto;
    margin: 0;
    padding: 20px 10px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: rgba(203, 58, 54, 1);
    margin: 10px 0;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
}

.about .about-item:hover {
    box-shadow: 1px 1px 10px 5px rgba(54, 58, 54, .6);
}

.about .about-item .about-img {
    width: 380px;
    height: 300px;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
}

.about .about-item .about-img:hover {
    box-shadow: 1px 1px 15px 10px rgba(211, 184, 141, 1);
}

.about .about-item .about-img img {
    width: 100%;
    height: 100%;
}

.about .about-item .about-para {
    width: 65%;
    height: 98%;
}

.about .about-item .about-para p {
    color: black;
    /* line-height: 25px; */
    width: 80%;
    font-size: 95%;
    margin: 10px auto;
    text-align: center;
}

.about .odd .about-img {
 order: -1;
}

.about .even .about-img {
    order: 1;
}
@media screen and (max-width: 1024px) {
    .about .about-item {
        flex-direction: column;
    }
    .about .about-item .about-img {
        width: 85%;
        order: -1 !important;
    }

    .about .about-item .about-para {
        width: 100%;
        height: auto;
    }

    .about .about-item .about-para p {
        font-size: 150%;
    }
}
@media screen and (max-width: 420px) {
    .about .about-item .about-para p {
        font-size: 97%;
    }
}

.ironman-projects-container {
    width: 100%;
    height: auto;
    padding: 0;
    margin: 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    align-content: center;
    flex-wrap: wrap;
}

.ironman-projects-container .project {
    border: 1px solid black;
    width: 550px;
    height: 500px;
    background-color: rgba(203, 58, 54, 1);
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
    margin: 15px 0;
}

.ironman-projects-container .project:hover {
    box-shadow: 1px 1px 10px 5px rgba(54, 58, 54, .6);
}

.ironman-projects-container .project .project-banner {
    width: 100%;
    height: 250px;
}

.ironman-projects-container .project .project-banner img {
    width: 100%;
    height: 100%;
}

.ironman-projects-container .project .project-info {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 15px;
    text-align: center;
}

.ironman-projects-container .project .project-info h1,
.ironman-projects-container .project .project-info h3 {
    padding: 0;
    margin: 0;
    color: rgba(211, 184, 141, 1);
    font-size: 100%;
}

.ironman-projects-container .project .project-info p {
    width: 90%;
    line-height: 20px;
    font-size: 90%;
}

@media screen and (max-width: 1024px) {
    .ironman-projects-container .project {
        width: 80%;
        height: auto;
    }
    .ironman-projects-container .project .project-info p {
        font-size: 140%;
    }
}

@media screen and (max-width: 779px) {
    .ironman-projects-container .project .project-info p {
        font-size: 110%;
    }
    .ironman-projects-container .project .project-info {
        padding: 0;
    }
}

@media screen and (max-width: 559px) {
    .ironman-projects-container .project {
        width: 100%;
        height: auto;

    }

    .ironman-projects-container .project .project-info p {
        font-size: 110%;
    }
}

.contact-ironman {
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 30px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.contact-ironman .contact-form-container .contact-title {
    padding: 10px;
    margin: 0;
    color: rgba(211, 184, 141, 1);
    font-size: 250%;
}

.contact-ironman .contact-form-container,
.contact-ironman .contact-form-container .contact-form {
    width: 50%;
    height: auto;
    /* padding: 10px; */
    margin: 5px 0;
    background-color: rgba(203, 58, 54, 1);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
}

.contact-ironman .contact-form-container:hover {
    box-shadow: 1px 1px 20px 2px rgba(203, 58, 54, 1);
}

.contact-ironman .contact-form-container .contact-form {
    width: 100%;
}

.contact-ironman .contact-form-container .contact-form .text-field {
    border: none;
    width: 90%;
    height: 40px;
    padding: 0;
    margin: 10px 0;
}

.contact-ironman .contact-form-container .contact-form .text-field input {
    width: 100%;
    height: 100%;
    outline: none;
    font-size: 120%;
    padding-left: 0;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
    text-align: center;
    border: none;
}

.contact-ironman .contact-form-container .contact-form .text-area {
    border: none;
    width: 90%;
    height: 150px;
    padding: 0;
    margin: 10px 0;
}

.contact-ironman .contact-form-container .contact-form .text-area textarea {
    width: 100%;
    height: 100%;
    outline: none;
    font-size: 120%;
    padding-left: 0;
    -webkit-transition-property: box-shadow;
    transition-property: box-shadow;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
    text-align: center;
    border: none;
}

.contact-ironman .contact-form-container .contact-form .text-area textarea:hover,
.contact-ironman .contact-form-container .contact-form .text-field input:hover {
    box-shadow: 1px 1px 3px 5px rgba(211, 184, 141, 1);
}

.contact-ironman .contact-form-container .contact-form .btn {
    width: 100%;
    height: 100%;
    outline: none;
    font-size: 120%;
    padding: 0;
    margin: 0;
    align-self: center;
    font-weight: bold;
    background-color: rgba(211, 184, 141, 1);
    color: rgba(203, 58, 54, 1);
    border: 1px solid rgba(211, 184, 141, 0);
    -webkit-transition-property: background-color border color;
    transition-property: background-color border color;
    -webkit-transition-duration: .8s;
            transition-duration: .8s;
}

.contact-ironman .contact-form-container .contact-form .btn:hover {
    background-color: rgba(203, 58, 54, 1);
    border: 1px solid rgba(211, 184, 141, 1);
    color: rgba(211, 184, 141, 1);
}

@media screen and (max-width: 920px) {
    .contact-ironman .contact-form-container{
        width: 98%;
    }
    .contact-ironman .contact-form-container .contact-form {
        width: 98%;
    }
}
.footer {
    width: 100%;
    height: 120px;
    padding: 20px 0;
    margin: 20px 0 0 0;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    background-color: rgba(36, 34, 31, 1);
}

.footer p,
.footer h1 {
    font-weight: bolder;
    color: rgba(211, 184, 141, 1);
    padding: 0;
    margin: 0;
}
